export default "import {Component, ViewEncapsulation} from \"@angular/core\";\nimport {IItemsReorderedEvent, IRepeatItem} from \"@nova-ui/bits\";\n\ninterface IRepeatCompanyItem extends IRepeatItem {\n    name: string;\n    disabled?: boolean;\n    preview?: string;\n}\n\n@Component({\n    selector: \"nui-repeat-drag-preview-example\",\n    templateUrl: \"./repeat-drag-preview.example.component.html\",\n    styleUrls: [\"./repeat-drag-preview.example.component.less\"],\n    encapsulation: ViewEncapsulation.None,\n})\nexport class RepeatDragPreviewExampleComponent {\n    public companies: IRepeatCompanyItem[] = [\n        {\n            name: \"Adobe\",\n            preview: \"https://upload.wikimedia.org/wikipedia/commons/thumb/7/7b/Adobe_Systems_logo_and_wordmark.svg/524px-Adobe_Systems_logo_and_wordmark.svg.png\",\n        },\n        {\n            name: \"IBM\",\n            preview: \"https://upload.wikimedia.org/wikipedia/commons/thumb/5/51/IBM_logo.svg/800px-IBM_logo.svg.png\",\n        },\n        {\n            name: \"Dell\",\n            preview: \"https://upload.wikimedia.org/wikipedia/commons/thumb/4/48/Dell_Logo.svg/300px-Dell_Logo.svg.png\",\n        },\n        {\n            name: \"Microsoft\",\n            preview: \"https://upload.wikimedia.org/wikipedia/commons/9/96/Microsoft_logo_%282012%29.svg\",\n        },\n    ];\n\n    public onItemsReordered(event: IItemsReorderedEvent<IRepeatCompanyItem>): void {\n        // update items according to the new order\n        this.companies = event.currentState;\n    }\n}\n";