export default "@import (reference) \"nui-framework-variables\";\n@import (reference) \"mixins\";\n.single-selection-example {\n    .blue {\n        background-color: #0000ff;\n    }\n\n    .green {\n        background-color: #009900;\n    }\n\n    .yellow {\n        background-color: #ffff00;\n    }\n\n    .orange {\n        background-color: #ff6600;\n    }\n\n    .cyan {\n        background-color: #00ffff;\n    }\n\n    .magenta {\n        background-color: #ff00ff;\n    }\n\n    .black {\n        background-color: #000000;\n    }\n\n    .flex-row-container {\n        display: flex;\n    }\n\n    .flex-row-item {\n        margin: 0 5px;\n    }\n\n    .flex-row-item:first-child {\n        margin-left: 0;\n    }\n\n    .flex-independent-row-item {\n        margin: 15px 5px;\n    }\n\n    .flex-column-container {\n        display: flex;\n        flex-direction: column;\n    }\n\n    .name-item {\n        font-size: 12px;\n        font-weight: bold;\n    }\n\n    .status-item {\n        font-size: 10px;\n        .setCssVariable(color, nui-color-text-secondary);\n    }\n\n    .gold {\n        font-size: 10px;\n        font-weight: bold;\n        .setCssVariable(color, nui-color-brand-four);\n    }\n\n    .platinum {\n        font-size: 10px;\n        font-weight: bold;\n        .setCssVariable(color, nui-color-brand-seven);\n    }\n\n    .bronze {\n        font-size: 10px;\n        font-weight: bold;\n        .setCssVariable(color, nui-color-brand);\n    }\n}\n";